import { useEffect, useRef } from 'react';

function getScrollOffset(element = { scrollTop: 0, scrollLeft: 0 }) {
  return {
    top: element.scrollTop,
    left: element.scrollLeft,
  };
}

function getPositionOffset(listEl, scrollableEl) {
  const scrollOffset = getScrollOffset(scrollableEl);
  const elementRect = listEl.getBoundingClientRect();
  const containerRect = scrollableEl.getBoundingClientRect();
  return {
    top: elementRect.top + scrollOffset.top - containerRect.top,
    left: elementRect.left + scrollOffset.top - containerRect.left,
  };
}

function isParent(parentNode, childNode) {
  return (
    childNode.parentNode === parentNode ||
    (childNode.parentNode && isParent(parentNode, childNode.parentNode))
  );
}

export default function useWindowScrollerList({ listInstance, outerListRef }) {
  const scrollableParent = useRef();
  const listOffset = useRef();

  const getElementOffset = () => {
    listOffset.current = getPositionOffset(
      outerListRef.current,
      scrollableParent.current,
    );
  };

  const handleScroll = e => {
    if (!scrollableParent.current) {
      const containerTarget = e.target === document ? document.body : e.target;
      scrollableParent.current =
        isParent(containerTarget, outerListRef.current) && containerTarget;
    }

    if (!scrollableParent.current) return;

    getElementOffset();

    const scrollOffset = getScrollOffset(scrollableParent.current);
    const scrollTop = Math.max(0, scrollOffset.top - listOffset.current.top);
    if (listInstance.current) listInstance.current.scrollTo(scrollTop);
  };

  useEffect(() => {
    window.addEventListener('scroll', handleScroll, true);
    window.addEventListener('resize', getElementOffset, true);
    return () => {
      window.removeEventListener('scroll', handleScroll, true);
      window.removeEventListener('resize', getElementOffset, true);
    };
  }, []);
}
