import { v4 as uuidv4 } from 'uuid';
import { useMemo, useRef, useState } from 'react';
import { orderBy } from 'lodash';
import {
  mergeInstance,
  registerHotKeysHookToInstance,
  registerStateHookToInstance,
} from './utils';

const defaultDecorators = {
  instance: [],
  state: [],
  hotKeys: [],
  renderers: [],
  composeData: [],
};

export default function createDataInstanceRef({
  uuid: uuidProp,
  data,
  renderers,
  plugins,
  customDecorators,
  ...props
}) {
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const instance = useRef();
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [uuid] = useState(uuidProp || (() => uuidv4()));

  instance.current = {
    uuid,
    state: {},
    actions: {},
    hotKeys: {}, // keyboard hotkeys to run specific actions
    originalData: data,
    records: [], // item abstraction with meta data for originalData
    positionedRecords: [], // Rendered items from the virtualization with its position
    renderers,
    ref(key) {
      // eslint-disable-next-line no-return-assign
      return (node) => (instance.current.refs[key] = node);
    },
    getInstance() {
      return instance.current;
    },
    props,
  };

  // eslint-disable-next-line react-hooks/rules-of-hooks
  const decorators = useMemo(() => {
    let nextDecorators = { ...defaultDecorators, ...customDecorators };

    plugins.forEach((plugin) => {
      if (!plugin) return;
      nextDecorators = plugin(nextDecorators);
    });

    Object.keys(nextDecorators).forEach((key) => {
      if (Array.isArray(nextDecorators[key]))
        nextDecorators[key] = orderBy(
          nextDecorators[key],
          (item) => item.order,
        );
    });

    return nextDecorators;
  }, []); // the plugins are defined on creation, but not changed at runtime

  // todo: test if getting the hooks without registering them
  //  here would be beneficial to memoize instance and squeeze performance
  instance.current = decorators.instance.reduce(
    mergeInstance,
    instance.current,
  );
  instance.current = decorators.state.reduce(
    registerStateHookToInstance,
    instance.current,
  );
  instance.current = decorators.hotKeys.reduce(
    registerHotKeysHookToInstance,
    instance.current,
  );
  instance.current.renderers = decorators.renderers.reduce(
    (result, decorator) => decorator(result, instance.current),
    renderers,
  );

  return { instance, decorators };
}
